.header {
  margin-top: 0;
}

.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Lobster', cursive;
  color: black;
}

.headerTitleTop {
  position: absolute;
  top: 5%;
  left: 5%;
  font-size: 20px;
}

.headerTitleBottom {
  position: absolute;
  top: 5%;
  left: 5%;
  font-size: 110px;
}

.headerImage {
  width: 100%;
  height: 300px;
}